import {
    Button,
    Space,
    FloatingPanel,
    Popup,
    ConfigProvider,
    ErrorBlock,
    setDefaultConfig,
    FloatingBubble,
    NavBar,
} from "antd-mobile";
import Settings, {ColorContext, ColorType, DaynightContext, SettingsContext, ThemeContext} from './Settings'
import {useContext} from "react";

export default function Pages({naviTitle, naviRight, onBack, children}) {
    const {settings, setSettings} = useContext(SettingsContext)
    return <div
        style={{
            width: "100%",
            height: '100%',
            fontSize: 20,
            fontWeight: "bold",
            color: "#000",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            backgroundColor: settings.color[0],
            position: 'relative',
        }}
    >
        <div style={{
            width: '100%', display: 'flex', backgroundColor: settings.color[3]
        }}>
            <NavBar
                style={{
                    width: '100%', color: settings.color[9],
                    fontSize: Settings.getFont(2)[0],
                    lineHeight: Settings.getFont(2)[1],
                }}
                onBack={onBack}
                right={naviRight}
            >{naviTitle}</NavBar>
        </div>
        {children}
    </div>
}
