import {
    Button,
    Space,
    FloatingPanel,
    Popup,
    ConfigProvider,
    ErrorBlock,
    FloatingBubble, Switch,
    // useConfig, setDefaultConfig, getDefaultConfig

} from "antd-mobile";
import {getDefaultConfig, setDefaultConfig, useConfig} from "antd-mobile/es/components/config-provider";
import {useContext, useState} from "react";
import Settings, {
    BlackEnum,
    ColorConfigs,
    ColorType,
    DaynightContext,
    ThemeContext,
    SettingsContext, GrayColor, BlackColor, ThemeColor,
} from './Settings'
import Main from "./Main";
import Pages from "./Pages";

export default function App() {
    const [route, setRoute] = useState('Main');
    const [theme, setTheme] = useState(Settings.getTheme())
    const [daynight, setDaynight] = useState(Settings.getDaynight())
    const [settings, setSettings] = useState({
        theme: Settings.getTheme(),
        daynight: Settings.getDaynight(),
        color: ThemeColor[Settings.getTheme()][Settings.getDaynight() == 'day' ? 0 : 1],
        black: BlackColor[Settings.getDaynight() == 'day' ? 0 : 1],
    })
    return (
        <ThemeContext.Provider value={{theme, setTheme}}>
            <DaynightContext.Provider value={{daynight, setDaynight}}>
                <SettingsContext.Provider value={{settings, setSettings}}>
                        <div
                            style={{
                                width: window.innerWidth, height: window.innerHeight,
                                position: "relative",
                                backgroundColor: '#fff',//settings.color[1],
                                display: "flex",
                                flexDirection: "column",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            {route == 'Main' ?
                                <Main
                                    onStart={() => {
                                        setRoute('Pages')
                                    }}
                                /> : <Pages/>}
                        </div>
                </SettingsContext.Provider>
            </DaynightContext.Provider>
        </ThemeContext.Provider>
    );
}
