import {
    Button,
    Space,
    FloatingPanel,
    Popup, Dropdown,
    ConfigProvider,
    ErrorBlock,
    setDefaultConfig,
    FloatingBubble,
    Switch, Divider,
} from "antd-mobile";
import {DropdownRef} from "antd-mobile/es/components/dropdown"
import {useContext, useRef, useState} from "react";
import {Menu} from "antd";
import MathPage from "./MathPage";
import PoetryPage from "./PoetryPage";
import EnglishPage from "./EnglishPage";
import Settings, {
    BlackEnum,
    ColorContext,
    ColorType,
    DaynightContext,
    ThemeContext,
    TextColorContext,
    Themes, ColorConfigs, SettingsContext, ThemeColor, BlackColor
} from "./Settings";
import {getDefaultConfig, useConfig} from "antd-mobile/es/components/config-provider";
// import {DropDownRef} from "antd-mobile/es/components/dropdown/dropdown";


// import DropdownRef　 from 'antd-mobile/es/components/dropdown/dropdown'

const MenuSettings = {
    Math: {
        title: '数学',
        key: 'Math',
        component: MathPage,
    },
    Poetry: {
        title: '诗词',
        key: 'Poetry',
        component: PoetryPage,
    },
    English: {
        title: '英语',
        key: 'English',
        component: EnglishPage,
    }
}
export default function Pages() {
    const {theme, setTheme} = useContext(ThemeContext)
    const {daynight, setDaynight} = useContext(DaynightContext)
    const {settings, setSettings} = useContext(SettingsContext)

    const [showSelection, setShowSelection] = useState(false);
    const [offset, setOffset] = useState({x: -24, y: -24})
    const [route, setRoute] = useState('Main');
    const [winSize, setWinSize] = useState({w: window.innerWidth, h: window.innerHeight})
    const dropdownRef = useRef()
    window.onresize = () => {
        setWinSize({
            w: window.innerWidth, h: window.innerHeight
        })
    };

    function getPage(route) {
        let Comp = MenuSettings[route].component
        return <Comp
            onBack={() => {
                setRoute('Main')
            }}
        />
    }

    function renderFloatingBubble() {
        return route == 'Main' ? null : <FloatingBubble
            style={{
                '--initial-position-bottom': '15px',
                '--initial-position-right': '15px',
                '--edge-distance': '60px 15px 15px 15px',
                '--background': settings.color[6],
            }}
            onOffsetChange={offset => {
                setOffset(offset)
            }}
            offset={offset}
            axis='xy'
            magnetic='xy'
            onClick={() => {
                setShowSelection(true)
            }}
            children={<span style={{fontSize: 20, color: '#fff'}}>M</span>}
        />
    }

    function renderMenuPopup() {
        function renderRow(title, right) {

            return <div
                style={{
                    width: '100%', height: 40,
                    display: 'flex', flexDirection: 'row',
                    alignItems: 'center',
                    marginBottom: 10,
                }}
            >
                <span style={{
                    width: 130,
                    fontSize: Settings.getFont(2)[0],
                    lineHeight: Settings.getFont(2)[1],
                    color: settings.color[9],
                }}>{title}：</span>
                <div style={{
                    height: '100%', flex: 1,
                    display: 'flex', flexDirection: 'row-reverse',
                    alignItems: 'center',
                }}>{right}</div>
            </div>
        }

        return <Popup
            maskStyle={{
                '--background-color': settings.black[BlackEnum.Background]
            }}
            closeOnMaskClick={true}
            visible={showSelection}
            onMaskClick={() => {
                dropdownRef.current?.close()
                setShowSelection(false)
            }}
            bodyStyle={{
                height: '80vh',
                display: 'flex', flexDirection: 'row',
                alignItems: 'center', justifyContent: 'center',
                backgroundColor: settings.color[4]
            }}
        >
            <div
                style={{
                    flex: 1, height: '100%',
                    display: 'flex', flexDirection: 'column',
                    alignItems: 'center',
                    // justifyContent: 'center',
                    padding: '40px 15px 15px 15px',
                }}
            >

                {renderRow('设置主题颜色', <Dropdown
                    ref={dropdownRef}
                    style={{
                        backgroundColor: 'transparent',
                    }}
                >
                    <Dropdown.Item
                        key={theme}
                        highlight={false}
                        title={theme}
                        style={{
                            fontSize: 20, fontWeight: 'bold',
                            color: settings.color[9],
                            overflowY: 'auto',
                        }}
                    >
                        <div
                            style={{
                                width: '100%',
                                height: winSize.h * 0.8 - 50,
                                overflowY: 'scroll',
                            }}
                        >
                            {Themes.map(key => {
                                return <div
                                    key={key}
                                    style={{
                                        width: '100%', height: 40,
                                        display: 'flex', flexDirection: 'row',
                                        backgroundColor: Settings.color(6, key, daynight),
                                        padding: '0px 20px',
                                        alignItems: 'center', cursor: 'pointer',
                                    }}
                                    onClick={() => {
                                        setTheme(key)
                                        Settings.saveTheme(key)
                                        let res = key
                                        setDaynight(res)
                                        Settings.saveDaynight(res)
                                        setSettings({
                                            theme: res,
                                            daynight: daynight,
                                            color: ThemeColor[res][daynight == 'day' ? 0 : 1],
                                            black: BlackColor[daynight == 'day' ? 0 : 1],
                                        })
                                        dropdownRef.current?.close()
                                    }}
                                >
                                    <span style={{fontSize: 16, fontWeight: 'bold', color: '#eee'}}>{key}</span>
                                </div>
                            })}
                        </div>
                    </Dropdown.Item>
                </Dropdown>)}
                {renderRow(daynight == 'day' ? '设为暗夜模式' : '设为白天模式', <Switch
                    uncheckedText='暗夜'
                    checkedText='白天'
                    style={{
                        '--checked-color': Settings.color(ColorType.Normal, theme, 'day'),
                        '--unchecked-color': Settings.color(ColorType.Normal, theme, 'night'),
                        // '--height': '36px',
                        // '--width': '60px',
                    }}
                    checked={daynight == 'day'}
                    onChange={(v) => {
                        let res = v ? 'day' : 'night'
                        setDaynight(res)
                        Settings.saveDaynight(res)
                        setSettings({
                            theme: theme,
                            daynight: res,
                            color: ThemeColor[theme][res == 'day' ? 0 : 1],
                            black: BlackColor[res == 'day' ? 0 : 1],
                        })
                    }}
                />)}
            </div>
            <div style={{height: '100%', width: 1, backgroundColor: settings.black[BlackEnum.Border]}}/>
            <div
                style={{
                    padding: '40px 15px 20px 15px',
                }}
            >
                <Space direction='vertical'>
                    {Object.keys(MenuSettings).map((key) => {
                        let item = MenuSettings[key]
                        return <Button
                            key={key}
                            style={{
                                minWidth: 80,
                                fontSize: Settings.getFont(2)[0],
                                lineHeight: Settings.getFont(2)[1],
                                '--background-color': settings.color[4],
                                '--text-color': settings.color[9],
                                '--border-color': settings.black[BlackEnum.Border]
                            }}
                            onClick={() => {
                                setShowSelection(false)
                                setRoute(key)
                            }}
                        >{item.title}</Button>
                    })}
                </Space>
            </div>
        </Popup>
    }

    return <div
        style={{
            width: "100%",
            height: '100%',
            fontSize: 30,
            fontWeight: "bold",
            color: "#000",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
            backgroundColor:settings.color[0],
        }}
    >
        {route == 'Main' ? <Space direction='vertical'>
            {Object.keys(MenuSettings).map((key) => {
                let item = MenuSettings[key]
                return <Button
                    key={key}
                    color='primary'
                    style={{
                        minWidth: 80,
                        fontSize: Settings.getFont(2)[0],
                        lineHeight: Settings.getFont(2)[1],
                    }}
                    onClick={() => {
                        setRoute(key)
                    }}
                >{item.title}</Button>
            })}
        </Space> : getPage(route)}
        {renderFloatingBubble()}
        {renderMenuPopup()}
    </div>
}
