const settings = {
    getFont(type = 1) {
        type = Math.max(0, type)
        type = Math.min(type, 9)
        return [FontSettings.FontSize[type] + 'px', FontSettings.LineHeight[type] + 'px']
    },
    gray(type = 1) {
        type = Math.max(0, type)
        type = Math.min(type, 9)
        return GrayColor[type]
    },
    getData(key) {
        let data = localStorage.getItem(key)
        return data && JSON.parse(data)
    },
    saveData(key, data) {
        localStorage.setItem(key, data ? JSON.stringify(data) : data);
    },
    getTheme() {
        let theme = this.getData('Theme')
        if (!theme) {
            theme = 'Purple'
            this.saveData('Theme', theme)
        }
        return theme
    },
    getDaynight() {
        let daynight = this.getData('Daynight')
        if (!daynight) {
            daynight = 'night'
            this.saveData('Daynight', daynight)
        }
        return daynight
    },
    saveTheme(theme) {
        this.saveData('Theme', theme)
    },
    saveDaynight(daynight) {
        this.saveData('Daynight', daynight)
    },
    color(type, theme, daynight) {
        return ThemeColor[theme] [daynight == 'day' ? 0 : 1][type]
    },
    // textColor(type = BlackEnum.PrimaryText, daynight) {
    //     return ColorConfigs.Black[type][daynight == 'day' ? 0 : 1]
    // },
    // grayColor(type = 1) {
    //     return ColorConfigs.Gray[type]
    // },
}
export default settings
