import {
    Button,
    Space,
    FloatingPanel,
    Popup,
    ConfigProvider,
    ErrorBlock,
    setDefaultConfig,
    FloatingBubble,
} from "antd-mobile";
import {useContext, useState} from "react";
import Settings, {  SettingsContext, } from "./Settings";

export default function Main({onStart}) {
    const {settings, setSettings} = useContext(SettingsContext)
    return <div
        style={{
            width: "100%",
            height: '100%',
            fontSize: 30,
            fontWeight: "bold",
            color: "#000",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
            backgroundColor: settings.color[0],
        }}
    >
        <Button
            color='primary' style={{minWidth: 80}}
            onClick={() => {
                onStart && onStart()
            }}
        >开始</Button>
    </div>
}
